import React, { useContext, useEffect } from "react"
import { GamesContext } from '../../context/GamesState'
import { handleSplash } from '../../utils/auth';

import NavBar from "./navbar"
import Auth from "./auth";
import Bitmoji from "./bitmoji"

const Splash = props => {
  const { onDarkMode, user, setUser } = useContext(GamesContext);
  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";

  useEffect(() => {
    handleSplash(setUser);
  }, []);

  return (
    <section className={modeClasses}>
      <NavBar />
      { user ?
        <div className="container landing-container">
          <div className="words-container">
            <h2 className="subtitle">
              Lobby
            </h2>
          </div>
          <div>Hello, {user.email}</div>
        </div> :
        <Auth />
      }
      <Bitmoji />
    </section>
  );
}

export default Splash;