import Cookies from "js-cookie";
import axios from 'axios';

const apiDomain = "http://localhost:3000/api";
const apiCall = axios.create({
    baseURL: apiDomain,
    headers: {
        Authorization: '',
    }
});

apiCall.interceptors.request.use(
    async (config) => {
        const cookie = getSessionCookie();
        if (cookie && cookie.id && cookie.token) {
            config.headers.Authorization = `Bearer ${cookie.token}`;
        }
        return config;
    },
    (error) => {
        console.log("Issue:", error)
        Promise.reject(error);
    }
)

const setSessionCookie = (sessionInfo) => {
    const { id, token } = sessionInfo;
    const expirationDate = new Date();
    expirationDate.setDate(expirationDate.getDate() + 365); // set cookie expiration to 365 days from now

    Cookies.set("id", id, {
        // expires: expirationDate,
        // httpOnly: true,
        // secure: true,
        // sameSite: "strict"
    });
    Cookies.set("token", token, {
        // expires: expirationDate,
        // httpOnly: true,
        // secure: true,
        // sameSite: "strict"
    });

    console.log("Set Cookie:", id, token);
};

const removeSessionCookie = () => {
    Cookies.remove("id");
    Cookies.remove("token");

    console.log("Removed Cookie");
}

const getSessionCookie = () => {
    const id = Cookies.get("id");
    const token = Cookies.get("token");

    console.log("Got Cookie:", id, token);

    if (id && token) {
        return {
            id: id,
            token: token
        };
    } else {
        return null;
    }
};

const handleSplash = async (setUser) => {
    const cookie = getSessionCookie();
    if (cookie && cookie.id && cookie.token) {
        apiCall.get(`/users/${cookie.id}`)
        .then((res) => {
            setUser(res.data);
        })
    }
}

const handleAuth = async (setUser, setAuthError, email) => {
    apiCall.post(`/auth/login/`, { email: email })
    .then((res) => {
        setSessionCookie(res.data);
        setUser(res.data);
    })
    .catch((e) => {
        // Modify with actual login validation later
        apiCall.post(`/auth/signup/`, { email: email })
        .then((res) => {
            setSessionCookie(res.data);
            setUser(res.data);
        })
        .catch((e) => {
            console.log("Error:", e);
            setAuthError(e);
        });
    });
};

const handleLogout = async (setUser, setAuthError) => {
    const cookie = getSessionCookie();
    if (cookie && cookie.id && cookie.token) {
        apiCall.post(`/auth/logout/${cookie.id}`)
        .then((res) => {
            setUser(null);
            removeSessionCookie();
        })
        .catch((e) => {
            console.log("Error:", e);
            setAuthError(e);
        });
    }
}

const handleDelete = async (setUser, setAuthError) => {
    const cookie = getSessionCookie();
    if (cookie && cookie.id && cookie.token) {
        apiCall.delete(`/users/${cookie.id}`)
        .then((res) => {
            setUser(null);
            removeSessionCookie();
        })
        .catch((e) => {
            console.log("Error:", e);
            setAuthError(e);
        });
    }
}

export { setSessionCookie, getSessionCookie, handleSplash, handleAuth, handleLogout, handleDelete };