import React, { useContext, useRef } from "react"
import { GamesContext } from '../../context/GamesState'
import "../../styles/auth.scss"
import { handleAuth } from '../../utils/auth';

const Auth = props => {
  const { onDarkMode, setUser, setAuthError } = useContext(GamesContext);
  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";
  const emailRef = useRef(null);

  const handleClick = (event) => {
    const email = emailRef.current.value;
    handleAuth(setUser, setAuthError, email);
  }

  return (
    <section className={modeClasses}>
      <div className="container">
      <div className="auth-container">
        <div className="words-container">
            <h2 className="subtitle">
              Sign Up or Login
            </h2>
        </div>

        <div>
          <label className={`auth-label ${modeClasses}`} htmlFor="email">Email</label>
          <input className={`auth-input ${modeClasses}`} type="text" id="email" name="email" ref={emailRef}></input>

          <button
            className={`auth-button ${modeClasses}`}
            onClick={handleClick}
            id="continueButton"
            value="continue"
          >
            <b>Continue</b>
          </button>
        </div>
      </div>
      </div>
    </section>
  );
}

export default Auth;